.btn-back {
  position: fixed;
  top: 25px;
  left: 25px;

  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 8px;

  color: white;
  background-color: #4E598C;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: 0.3s;
}

.btn-back:hover{
  color: white;
  background-color: #333b5b;

  transform: scale(1.1);
}

.profile-img {
  display: flex;
  justify-content: center;
  margin: 15px 0;

  img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    object-fit: cover;
  }
}

.illustration {
  text-align: center;

  img {
    width: 60%;
    align-self: center;
  }

  p {
    margin: 8px 0 15px 0;
  }
}

@media (min-width: 992px) {
  .article-container {
    margin: 20px 15%;
  }
}

@media (max-width: 992px) {
  .illustration {
    img {
      width: 90%;
    }
  }

  .btn-back {
    display: none;
  }
}
