$badge-size: 30px;
$margin-item-left: 50px;
$border-radius: 8px;

$innovation-color: #DDE0BD;
$international-color: #E0A458;
$seminar-color: #F7EF99;
$personal-color: #E637BF;
$engagement-color: #419D78;
$time-color: #9FA0C3;
$article-color: #87F1FF;
$app-color: #4E598C;

.legend {
  position: fixed;
  top: 25px;
  left: 25px;

  .legend-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    p {
      margin: 0 0 0 8px;
    }
  }
}

.timeline {
  border-left: 2px solid lightgray;

  margin: 0 3px;
  padding-right: 35px;
}

.timeline-label {
  position: relative;
  left: 35px;
  width: 80%;
  margin: 0;
  padding: 8px 0;

  font-weight: bold;
}

.timeline-item {
  height: auto;
  display: grid;

  padding: 16px 0;

  a {
    text-decoration: none;
    color: inherit;
  }
}

.timeline-panel, .timeline-pin{
  grid-row: 1;
  grid-column: 1;
}

.timeline-panel {
  position: relative;
  left: 35px;
  width: 100%;

  border-radius: $border-radius;

  -webkit-box-shadow: 0 0 10px -1px rgba(211, 211, 211, 1);
  -moz-box-shadow: 0 0 10px -1px rgba(211, 211, 211, 1);
  box-shadow: 0 0 10px -1px rgba(211, 211, 211, 1);

  p {
    margin: 0;
  }

  .card-img {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: $border-radius;
    border-top-right-radius: $border-radius;
  }

  .panel-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    h5 {
      margin: 0;
    }
  }

  .badge-location {
    color: white;
    background-color: $app-color;
  }

  .card-footer {
    display: flex;
    justify-content: center;

    p {
      margin: 0 8px;
    }
  }
}

.timeline-pin {
  position: relative;
  display: block;
  top: 8px;
  left: -($badge-size/2);
}

.pin {
  width: $badge-size;
  height: $badge-size;

  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50%;

  &-seminar {
    @extend .pin;
    background-color: $seminar-color;
  }

  &-international {
    @extend .pin;
    background-color: $international-color;
  }

  &-personal {
    @extend .pin;
    background-color: $personal-color;
  }

  &-engagement {
    @extend .pin;
    background-color: $engagement-color;
  }

  &-innovation {
    @extend .pin;
    background-color: $innovation-color;
  }

  &-time {
    @extend .pin;
    background-color: $time-color;
  }

  &-article {
    @extend .pin;
    background-color: $article-color;

    top: $badge-size + 16px;
  }
}

@media (min-width: 992px) {
  .timeline {
    margin: 0 20%;
  }

  .clickable {
    transition: 0.3s;
  }

  .clickable:hover {
    transform: scale(1.02);
  }

  .non-clickable {
    cursor: initial;
  }
}

@media (max-width: 992px) {
  .legend {
    display: none;
  }
}
